import { renderHome } from './views/home';
import { renderAbout } from './views/about';
import { renderAdd } from './views/add';
import { renderLogin } from './views/login';
import { renderRegister } from './views/register';
import { renderStoryDetail } from './views/story-detail';
import { renderOfflineStories } from './views/offline';

const routes = {
  '/': renderHome,
  '/home': renderHome,
  '/about': renderAbout,
  '/add': renderAdd,
  '/login': renderLogin,
  '/register': renderRegister,
  '/offline': renderOfflineStories,
};

function loadPage() {
  console.log('[Router] loadPage called.');
  const app = document.getElementById('app');
  if (!app) { console.error('[Router] #app element not found!'); return; }

  const hash = location.hash || '#/';
  let routePath = hash.replace(/^#/, '');
  if (routePath && !routePath.startsWith('/')) { routePath = '/' + routePath; }
  else if (routePath === '') { routePath = '/'; }
  console.log(`[Router] Path: "${routePath}"`);

  const storyMatch = routePath.match(/^\/story\/(.+)/);
  if (storyMatch) {
    const storyId = storyMatch[1];
    console.log(`[Router] Matched /story/ ID: ${storyId}`);
    const renderDetailFunc = renderStoryDetail;
    const render = () => renderDetailFunc(app, storyId);
    if (document.startViewTransition) { document.startViewTransition(render); }
    else { render(); }
    return;
  }

  const render = routes[routePath];
  console.log(`[Router] Lookup for "${routePath}":`, render ? 'Found' : 'Not Found');

  if (!render) {
    console.error(`[Router] Route not found for path: "${routePath}"`);
    app.innerHTML = '<p>Halaman tidak ditemukan</p>';
    return;
  }

  console.log(`[Router] Rendering route "${routePath}"`);
  const doRender = () => render(app);
  if (document.startViewTransition) { document.startViewTransition(doRender); }
  else { doRender(); }
}

export function initRouter() {
  window.removeEventListener('hashchange', loadPage);
  window.removeEventListener('load', loadPage);
  window.addEventListener('hashchange', loadPage);
  window.addEventListener('load', loadPage);
  console.log("[Router] Router initialized.");
}