import '../styles/style.css';
import { initRouter } from './router';
import 'leaflet/dist/leaflet.css';
import { requestNotificationPermission, subscribePush, unsubscribePush, getSubscriptionState } from './push-subscription';

document.addEventListener('DOMContentLoaded', async () => {
  initRouter();
  setupNavigationDrawer();
  updateLoginStatusUI(); // Initial check
});

function setupNavigationDrawer() {
    const drawerButton = document.getElementById('drawer-button');
    const navigationDrawer = document.getElementById('navigation-drawer');
    const pushSubscribeButton = document.getElementById('push-subscribe-button');
    const pushUnsubscribeButton = document.getElementById('push-unsubscribe-button');

    // Drawer toggle might be handled by inline script, but ensure it exists
    if (drawerButton && navigationDrawer && !drawerButton.getAttribute('listener-attached')) {
         drawerButton.addEventListener('click', () => { navigationDrawer.classList.toggle('open'); });
         drawerButton.setAttribute('listener-attached', 'true'); // Prevent double listeners
    }

    // Close drawer logic
    if(navigationDrawer) {
        document.body.addEventListener('click', (event) => { if (!navigationDrawer.contains(event.target) && drawerButton && !drawerButton.contains(event.target)) { navigationDrawer.classList.remove('open'); } });
        navigationDrawer.addEventListener('click', (event) => { if (event.target.tagName === 'A' && event.target.getAttribute('href')?.startsWith('#')) { navigationDrawer.classList.remove('open'); } });
    }

    // Attach Push Button Logic
    if (pushSubscribeButton) {
        pushSubscribeButton.addEventListener('click', async () => {
            const token = localStorage.getItem('token'); if (!token) { alert('Silakan login terlebih dahulu.'); return; }
            pushSubscribeButton.disabled = true; pushSubscribeButton.textContent = 'Subscribing...';
            try { await requestNotificationPermission(); await subscribePush(token); alert('Berhasil berlangganan!'); updatePushSubscriptionUI(); }
            catch (err) { console.error('Gagal berlangganan:', err); alert(`Gagal berlangganan: ${err.message || 'Silakan coba lagi.'}`); updatePushSubscriptionUI(); }
            if (navigationDrawer) navigationDrawer.classList.remove('open');
        });
    }

    if (pushUnsubscribeButton) {
        pushUnsubscribeButton.addEventListener('click', async () => {
            const token = localStorage.getItem('token'); if (!token) return;
            pushUnsubscribeButton.disabled = true; pushUnsubscribeButton.textContent = 'Unsubscribing...';
            try { await unsubscribePush(token); alert('Berhasil berhenti berlangganan.'); updatePushSubscriptionUI(); }
            catch (err) { console.error('Gagal berhenti berlangganan:', err); alert(`Gagal berhenti berlangganan: ${err.message || 'Silakan coba lagi.'}`); updatePushSubscriptionUI(); }
            if (navigationDrawer) navigationDrawer.classList.remove('open');
        });
    }
}

async function updatePushSubscriptionUI() {
    const pushSubscribeButton = document.getElementById('push-subscribe-button');
    const pushUnsubscribeButton = document.getElementById('push-unsubscribe-button');
    const subscribeLi = document.getElementById('push-subscribe-li');
    const unsubscribeLi = document.getElementById('push-unsubscribe-li');
    const isLoggedIn = !!localStorage.getItem('token');
    console.log('updatePushSubscriptionUI called. isLoggedIn:', isLoggedIn);

    if (!subscribeLi || !unsubscribeLi || !pushSubscribeButton || !pushUnsubscribeButton) { console.warn("Push UI elements not found."); return; }

    pushSubscribeButton.disabled = false; pushSubscribeButton.innerHTML = '<i class="fas fa-bell"></i> Subscribe';
    pushUnsubscribeButton.disabled = false; pushUnsubscribeButton.innerHTML = '<i class="fas fa-bell-slash"></i> Unsubscribe';

    if (!isLoggedIn || !('Notification' in window) || !('serviceWorker' in navigator) || !('PushManager'in window)) {
        console.log('Hiding push buttons (not supported/logged in).');
        subscribeLi.style.display = 'none'; unsubscribeLi.style.display = 'none'; return;
    }
    try {
        const state = await getSubscriptionState(); console.log("Push subscription state:", state);
        if (state.isSubscribed) { console.log('Showing Unsubscribe button.'); subscribeLi.style.display = 'none'; unsubscribeLi.style.display = 'list-item'; }
        else if (state.permission === 'granted') { console.log('Showing Subscribe button.'); subscribeLi.style.display = 'list-item'; unsubscribeLi.style.display = 'none'; }
        else { console.log('Showing Subscribe button (permission not granted/default).'); subscribeLi.style.display = 'list-item'; unsubscribeLi.style.display = 'none'; if (state.permission === 'denied') { pushSubscribeButton.disabled = true; pushSubscribeButton.textContent = 'Permission Denied'; } }
    } catch (error) { console.error("Error updating push UI:", error); subscribeLi.style.display = 'none'; unsubscribeLi.style.display = 'none'; }
}

function updateLoginStatusUI() { updatePushSubscriptionUI(); }
window.appUpdateLoginStatusUI = updateLoginStatusUI; // Expose for login/logout calls

console.log('[App] updatePushSubscriptionUI running...');
