// src/presenters/addPresenter.js
export async function postStory(token, formData) {
  const response = await fetch('https://story-api.dicoding.dev/v1/stories', {
    method: 'POST',
    headers: { Authorization: 'Bearer ' + token },
    body: formData,
  });

  if (!response.ok) {
     let errorMessage = `HTTP error! status: ${response.status} ${response.statusText}`;
     try { const errorData = await response.json(); errorMessage = errorData.message || errorMessage; }
     catch (e) { console.warn("Could not parse error response as JSON."); }
     throw new Error(errorMessage);
  }
  const data = await response.json();
  if (data.error) { throw new Error(data.message || 'API error flag.'); }
  return data;
}